<template>
  <div id="app">
    <section class="todoapp">
      <header class="header">
        <h1>Labs</h1>
        <input class="new-todo"
          autofocus autocomplete="off"
          placeholder="What lab needs to be found?"
          v-model="filterTitle">
      </header>
      <section class="main" v-show="todos.length" v-cloak>
        <input class="toggle-all" type="checkbox" v-model="allDone">
        <ul class="todo-list">
          <li v-for="todo in filteredTodos"
            class="todo"
            :key="todo.date"
            :class="{ editing: todo == editedTodo }">
            <div class="view">
              <input class="toggle" type="checkbox" v-model="todo.completed">
              <label @dblclick="editTodo(todo)">
                <el-row>
                  <el-col :span="16">
                    <div style="margin: auto 0" class="caption">
                      <div style="font-size: 40px">{{todo.title}}</div>
                      <div style="font-size: 10px">{{todo.date}}</div>
                    </div>
                  </el-col>
                  <el-col :span="8">
                    <div style="padding: 20px 0;">
                      <el-tag style="float: right; margin: auto 0 auto 5px;" v-for="tag, tagIndex in todo.tags" :closable="true" type='success' :key='tag' :close-transitino='true' @close='closeTag(todo, tagIndex)'>
                        {{tag}}
                      </el-tag>
                    </div>
                  </el-col>
                </el-row>
              </label>
              <button class="destroy" @click="gotoDetail(todo)"><i class="el-icon-share"></i></button>
            </div>
            <input class="edit" type="text"
              v-model="todo.tag"
              v-todo-focus="todo == editedTodo"
              @blur="doneEdit(todo)"
              @keyup.enter="doneEdit(todo)"
              @keyup.esc="cancelEdit(todo)">
          </li>
        </ul>
      </section>
      <footer class="footer" v-show="todos.length" v-cloak>
        <span class="todo-count">
          <strong>{{ remaining }}</strong> {{ remaining | pluralize }} left
        </span>
        <ul class="filters">
          <li><a href="#/all" :class="{ selected: visibility == 'all' }">All</a></li>
          <li><a href="#/active" :class="{ selected: visibility == 'active' }">Active</a></li>
          <li><a href="#/completed" :class="{ selected: visibility == 'completed' }">Completed</a></li>
        </ul>
        <button class="clear-completed" @click="removeCompleted" v-show="todos.length > remaining">
          Clear completed
        </button>
      </footer>
    </section>
    <el-dialog title="细节" v-model="detailVisible">
      <el-table :data="detailTodo" :row-class-name="detailClass" @row-click="row_click">
        <el-table-column property="date" label="日期" width="150"></el-table-column>
        <el-table-column property="title" label="名称" width="200"></el-table-column>
        <el-table-column property="path" label="路径" width="200"></el-table-column>
        <el-table-column property="tags" label="标签" width="100"
         :filters="allTagsList"
         :filter-method="filterTag"
         inline-template>
         <el-tag v-for="tag in row.tags" :type='primary' close-transition>{{tag}}</el-tag>
       </el-table-column>
      </el-table>
    </el-dialog>
    <footer class="info">
      <p>Double-click to edit a lab's tag</p>
      <p>Written by <a href="http://evanyou.me">Wenxiao Chen</a></p>
      <p>Part of <a href="http://todomvc.com">Opprentice</a></p>
    </footer>
  </div>
</template>


<script>

  var STORAGE_KEY = 'todos-vuejs-2.0'
  var todoStorage = {
    fetch: function (app) {
      return app.$http.get('/_snapshot').then(function(res){
        app.todos = [];
        app.collects = {};
        var title = null;
        for (var i in res.data) {
          var todo = res.data[i];
          todo.path = todo[0];
          todo.tags = todo[1];
          todo.completed = todo[2];
          todo.date = todo[3];
          todo.title = todo.path.substr(0, todo.path.lastIndexOf('/'));
          if (app.collects[todo.title] == null)
            app.collects[todo.title] = [];
          app.collects[todo.title].push(todo);
          console.log(todo);
        }
        for (var i in app.collects) {
          var todo = {
            title: '',
            completed: true,
            tags: [0, 0],
            path: ''
          };
          for (var j in app.collects[i]) {
            var item = app.collects[i][j];
            todo.title = item.title;
            todo.completed = todo.completed & item.completed;
            todo.tags[todo.completed ? 1 : 0] += 1;
            todo.path = item.path;
          }
          app.todos.push(todo);
        }
        todoStorage.uid = app.todos.length
      }, function(res) {
        console.log('Error');
      });
    },

    save: function (todos) {
      //localStorage.setItem(STORAGE_KEY, JSON.stringify(todos))
    }
  }

  var filters = {
    all: function (todos) {
      return todos
    },

    active: function (todos) {
      return todos.filter(function (todo) {
        return !todo.completed
      })
    },

    completed: function (todos) {
      return todos.filter(function (todo) {
        return todo.completed
      })
    },

    title: function(todos, search) {
      var title = search.split(':')[0];
      var tag = search.split(':')[1];
      return todos.filter(function(todo) {
        if (todo.path.indexOf(title) >= 0) {
          if (tag) {
            for (var t in todo.tags) {
              if (todo.tags[t].indexOf(tag) >= 0)
                return true
            }
          } else {
            return true
          }
        }
        return false
      })
    }
  }

  export default {
    data: function() {
      return {
        todos: [],
        collects: {},
        newTodo: '',
        editedTodo: null,
        visibility: 'all',
        filterTitle: '',
        detail: ''
      }
    },

    mounted: function() {
      todoStorage.fetch(this);
    },

    watch: {
      '$route': function(to, from) {
        var path = to.path.replace(/\//, '');
        if (filters[path]) {
          this.$set(this, 'visibility', path)
        } else {
          this.$set(this, 'visibility', 'all')
        }
      },

      todos: {
        handler: function (todos) {
          todoStorage.save(todos)
        },
        deep: true
      }
    },

    computed: {
      filteredTodos: function () {
        console.log(this.todos);
        var todos = filters[this.visibility](this.todos);
        return filters['title'](todos, this.filterTitle)
      },

      remaining: function () {
        return this.filteredTodos.length
      },

      allDone: {
        get: function () {
          return this.remaining === 0
        },
        set: function (value) {
          this.todos.forEach(function (todo) {
            todo.completed = value
          })
        }
      },

      detailTodo: function() {
        return this.map[this.detail];
      },

      allTagsList: function() {
        var map = {};
        for (var i in this.detailTodo) {
          var tags = this.detailTodo[i].tags;
          for (var j in tags)
            map[tags[j]] = true;
        }
        var list = [];
        for (var i in map)
          list.push(map[i]);
        return list;
      }
    },

    filters: {
      pluralize: function (n) {
        return n === 1 ? 'lab' : 'labs'
      }
    },

    methods: {
      gotoDetail: function (todo) {
        this.detail = todo.title;
        window.open(todo.path + "/_navigation.html")
      },

      editTodo: function (todo) {
        this.editedTodo = todo
      },

      doneEdit: function (todo) {
        if (!this.editedTodo) {
          return
        }
        this.editedTodo = null
        if (todo.tag) {
          todo.tag = todo.tag.trim()
          todo.tags.push(todo.tag)
        }
      },

      cancelEdit: function (todo) {
        this.editedTodo = null
      },

      removeCompleted: function () {
        this.todos = filters.active(this.todos)
      },

      closeTag: function(todo, index) {
        if (index >=0 && index < todo.tags.length)
          todo.tags.splice(index, 1)
      },

      detailClass: function(row, index) {
        if (this.detailTodo[index].completed)
          return 'info-row';
        else
          return 'positive-row'
      },

      filterTag(value, row) {
        for (var i in row.tags)
          if (row.tags[i] == value)
            return true;
        return false;
      },

      row_click(row, e) {
        console.log(row);
        console.log(e);
      }
    },

    directives: {
      'todo-focus': function (el, value) {
        if (value) {
          el.focus()
        }
      }
    }
  }

</script>

<style>
  @import 'css/todo.css';
  .el-table .info-row {
    background: #c9e5f5;
  }

  .el-table .positive-row {
    background: #e2f0e4;
  }
</style>



